@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
.room {
    box-shadow: 1px 1px 10px rgba(0, 0, 0, .1);
    border-radius: 5px;
}
.room .heading {
    font-family: 'DM Sans';
    color: #464646;
    font-size: 2rem;
    font-weight: 500;
    z-index: 1000;
}
.room .messages {
    background: #eaeaea;
}
.room .messages .message .msg {
    color: rgba(0, 0, 0, .7);
}
.room .messages .my-message {
    align-self: flex-end;
}
.room .messages .my-message .user {
    display: none;
}
.room .messages .my-message .msg {
    color: white;
    background: #408eea;
}
.room .messages > div {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: auto !important;
    overflow: auto;
    min-height: 400px;
    max-height: 400px;
    padding: 1rem;
}
.room .form {
    position: relative;
    z-index: 1000;
}
.room .form input {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0px 0px 10px 10px;
}
.room .form button {
    position: absolute;
    right: 1rem;
    top: 1rem;
}
::-webkit-input-placeholder {
    opacity: 0.5;
}
:-ms-input-placeholder {
    opacity: 0.5;
}
::placeholder {
    opacity: 0.5;
}
@media (max-width: 30em) {
    .room .messages {
        flex: 1 1;
   }
}

